.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
    border-radius: 50px;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.debug {
    display: flex;
    flex-direction: row;
    max-width: 100%;
    overflow-x: hidden;
    height: 100vh;
    /* Ensure it takes the full viewport height */
}

/* .content-container {
    flex: 0 0 75%;
} */

.content-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-y: hidden;
    /* Adds scroll to the container if content overflows */
}

body {
    /* rgb(41, 41, 41) */
    background: rgb(41, 41, 41);
}